@import "variables";

.photos {
  $gap: 20px;
  display: flex;
  justify-content: space-between;

  &__img {
    width: calc(50% - #{$gap / 2});
    height: 260px;
    object-fit: cover;
    object-position: center;
    transition-duration: $transition-duration;
  }

  &__img:hover {
    transition-duration: $transition-duration;
    transform: scale(1.5);
    z-index: 3;
  }

  &--wide-left {
    .photos__img:first-child {
      width: calc(67% - #{$gap / 2});
    }

    .photos__img:last-child {
      width: calc(33% - #{$gap / 2});
    }
  }

  &--wide-right {
    .photos__img:first-child {
      width: calc(33% - #{$gap / 2});
    }

    .photos__img:last-child {
      width: calc(67% - #{$gap / 2});
    }
  }
}

@media screen and (max-width: $small-screen-breakpoint) {
  .photos {
    flex-direction: column;

    &__img {
      width: 99vw;
      margin: 10px 0;

      &:hover {
        transform: scale(1);
      }
    }

    &--wide-left {
      .photos__img:first-child {
        width: 99vw;
      }
  
      .photos__img:last-child {
        width: 99vw;
      }
    }

    &--wide-right {
      .photos__img:first-child {
        width: 99vw;
      }
  
      .photos__img:last-child {
        width: 99vw;
      }
    }
  }
}
